import { HandleProvider } from './../../../providers/handle/handle';
import { STORAGE_KEY } from './../../../services/constants';
import { StorageService } from './../../../services/storage-service';
import { Utility } from './../../../services/utility';
import { Session } from './../../../services/session';
import { BetaPageRouter } from '../beta.config'

import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { SqliteProvider } from '../../../providers/sqlite-provider';

/**
 * Generated class for the BetaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-beta',
  templateUrl: 'beta.html',
})
export class BetaPage {

  communityUrl: string;
  pages: any[] = BetaPageRouter;
  MassFlag: boolean = false;
  showSocial: boolean = false;
  isShowScreenShot: boolean = false;
  isOnlyRecordError: boolean = true;
  enabledLocalSetting: boolean = false;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public sqlite: SqliteProvider,
    public events: Events,
    public session: Session,
    public storage: StorageService,
    public hand: HandleProvider,
  ) { 
    this.init() 
  }

  async init() {
    this.enabledLocalSetting = await this.storage.getItem(STORAGE_KEY.SETTING.ENABLE_LOCAL_SETTING_SOCIAL);  
    this.MassFlag = await this.storage.getItem(STORAGE_KEY.SETTING.SHOWMASS);
    this.showSocial = await this.storage.getItem(STORAGE_KEY.SETTING.SHOWSOCIAL);

    this.isOnlyRecordError = this.hand.isOnlyRecordError;  
  }

  changeCollectFlag() {
    this.hand.isOnlyRecordError = this.isOnlyRecordError;
    this.storage.setItem(STORAGE_KEY.SETTING.ONLY_COLLECT_ERROR_INFO, this.isOnlyRecordError);
  }
  communityUrlChange() {
    this.communityUrl
  }
  setMassFlag() {
    this.session.user.showMass = this.MassFlag
    this.storage.setItem(STORAGE_KEY.SETTING.SHOWMASS, this.MassFlag);
    this.events.publish('MassFlagChange')
  }
  setshowSocialFlag() {
    this.session.user.showSocial = this.showSocial
    this.storage.setItem(STORAGE_KEY.SETTING.SHOWSOCIAL, this.showSocial);
    this.events.publish('MassFlagChange')
  }

  setEnableMassFlag(){
    this.storage.setItem(STORAGE_KEY.SETTING.ENABLE_LOCAL_SETTING_SOCIAL, this.enabledLocalSetting);
    this.events.publish('MassFlagChange')
  }

  showButton() {
    this.events.publish('beta: showScreenShotButton', this.isShowScreenShot)
  }

  openPage(item) {
    this.navCtrl.push(item.url, item.params)
  }
}



